.navBarBackground {
  height: 96px;
  display: flex;
  justify-content: flex-end;
  padding-right: 2vw;
  position: fixed;
  width: 100%;
  background: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.navBarMenu {
  font-size: 24px;
  height: 100%;
  padding: 0 24px;
  display: flex;
  align-items: center;
  font-family: 'Prompt';
  cursor: pointer;
}

